<template>
  <div>
    <section class="security" v-if="net">
      <h2>常见的漏洞和暴露(CVE)摘要</h2>
      <div class="data-statistics ">
        <el-row :gutter="18">
          <el-col :span="5">
            <div class="grid-content bg-purple">
              <div class="security-important">
                <p>
                  <i class="iconfont icon-system-fn-warning  security-warning icon-red">
                    &#xe638;</i>低危漏洞</p>
                <p>{{net.level.High}}</p>
              </div>
            </div>
          </el-col>
          <el-col :span="5">
            <div class="grid-content bg-purple">
              <div class="security-middle">
                <p>
                  <i class="iconfont icon-system-fn-warning security-warning icon-orange">
                    &#xe638;</i>中间漏洞</p>
                <p>{{net.level.Medium}}</p>
              </div>
            </div>
          </el-col>
          <el-col :span="5">
            <div class="grid-content bg-purple">
              <div class="security-highest">
                <p>
                  <i class="iconfont icon-system-fn-warning security-warning icon-yellow">
                    &#xe638;</i>低危漏洞</p>
                <p>{{net.level.Low}}</p>
              </div>
            </div>
          </el-col>
          <el-col :span="5">
            <div class="grid-content bg-purple">
              <div class="security-lowest">
                <p>
                  <i class="iconfont icon-system-fn-warning security-warning icon-blue">
                    &#xe638;</i>轻微漏洞</p>
                <p>{{net.level.Negligible}}</p>
              </div>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="grid-content bg-purple">
              <div class="security-unknown">
                <p>
                  <i class="iconfont icon-docker index-warning security-warning icon-black">
                    &#xe638;</i>未知漏洞</p>
                <p>{{net.level.Unknown}}</p>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </section>
    <section class="table-wrapper">
      <h2>CVE 列表{{this.quertList}}</h2>
      <div class="securitylists-text">
        <p class="security-search-box">
          <input class="search-text" type="text"
                 placeholder="请输入搜索的 CVE ID" v-model="cveKey">
          <button class="search-btn btn" @click="searchCve">搜索</button>
        </p>
      </div>
      <security-table v-bind:quertList="quertList"></security-table>
    </section>
  </div>
</template>

<script type="text/ecmascript-6">
  import securityTable from './components/securityTable'

  export default {
    props: ['net'],
    name: 'security',
    components: {
      securityTable
    },
    data() {
      return {
        cveKey: '',
        cveIndex: '',
        cveData: '',
        quertList: {
          image: this.net.image.replace('/', '@'),
          cveData: ''
        }
      }
    },
    methods: {
      strMapToObj(strMap) {
        const obj = Object.create(null)
        // eslint-disable-next-line
        for (const [k, v] of strMap) {
          obj[k] = v
        }
        return obj
      },
      searchCve() {
        this.quertList = null
        this.net.vulnerabilities.forEach((item, index) => {
          if (this.cveKey === item.vulnerability) {
            this.cveIndex = index
          }
        })
        // eslint-disable-next-line
        var cveList = new Map()
          .set('image', this.net.image.replace('/', '@'))
        if (this.cveKey !== '') {
          cveList.set('cveData', this.net.vulnerabilities[this.cveIndex])
        } else {
          cveList.set('cveData', '')
        }
        this.quertList = this.strMapToObj(cveList)
      }
    },
    mounted() {
      window.addEventListener('keyup', (event) => {
        if (event.keyCode === 13) {
          this.searchCve()
        }
      })
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .bg-purple
    background: #FAFAFA

  .grid-content
    height: 92px;

  .el-input {
    width: 336px;
  }

  .el-input-group__prepend {
    background-color: #fff;
  }

  .securitylists-text
    p
      display inline-block
      .search-text
        width: 300px
        height: 30px
        border: #5286F7 1px solid
        color: #808080
        padding: 0 0 0 15px
        font-size: 14px
        margin: 30px 0 0 0
        display: inline-block
      .search-btn
        height 32px
        width 50px
        background: #3E7AF6
        border: 0px
        color: #FFFFFF
        font-size: 14px
        line-height: 32px
        margin: 0px 0 0 -5px

  .data-statistics
    margin: 45px 0 0 0
    padding: 0 0 50px 0
    border-bottom: #E5E5E5 1px solid
    div[class^="security"]
      padding: 25px 0 0 0
      p
        text-align: center
        color: #666666
        font-size: 1.3em
        margin: 0 0 0 -20px
        i
          display: inline-block
          margin: 0 15px 0 0
      p:last-child
        position: relative
        text-align left
        left: 40%
        color: #4D4D4D
        font-size: 2.0em
        font-weight: 600
        line-height: 40px
        margin: 0

  .security-warning
    font-size: 1.45em

  .icon-red
    color: #D81E06

  .icon-orange
    color: #F27935

  .icon-yellow
    color: #F9BF3B

  .icon-blue
    color: #08B1D5

  .icon-black
    color: #1A1A1A
</style>



