<template>
  <div class="security-table">
    <div class="ibox-content">
      <table>
        <thead>
        <tr>
          <th>CVE ID</th>
          <th>严重程度</th>
          <th>漏洞包</th>
          <th>CVE-描述</th>
          <th>CVE-URL</th>
        </tr>
        </thead>
        <tbody>
        <tr v-if="this.quertList.cveData !== ''">
          <td>{{this.quertList.cveData.vulnerability}}</td>
          <td>{{this.quertList.cveData.severity}}</td>
          <td>{{this.quertList.cveData.featurename}}{{this.quertList.cveData.featureversion}}</td>
          <td>
            <el-tooltip class="item" effect="dark" placement="bottom">
              <div slot="content">{{this.quertList.cveData.description}}</div>
              <p class="item-description">{{this.quertList.cveData.description}}</p>
            </el-tooltip>
          </td>
          <td><a :href="this.quertList.cveData.link" target="_blank">
            {{this.quertList.cveData.link}}</a></td>
        </tr>
        <tr v-else v-for="item in netList" :key="item.id">
          <td>{{item.vulnerability}}</td>
          <td>{{item.severity}}</td>
          <td>{{item.featurename}}{{item.featureversion}}</td>
          <td>
            <el-tooltip class="item" effect="dark" placement="bottom">
              <div slot="content">{{item.description}}</div>
              <p class="item-description">{{item.description}}</p>
            </el-tooltip>
          </td>
          <td><a :href="item.link" target="_blank">{{item.link}}</a></td>
        </tr>
        </tbody>
      </table>
      <div class="divblock">
        <el-pagination
          background
          @current-change="handleCurrentChange"
          :current-page.sync="listQuery.page"
          :page-size.sync="listQuery.limit"
          layout="total, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import { fetchCVE } from '@/api/transaction'

  export default {
    props: ['quertList'],
    data() {
      return {
        total: null,
        netList: [],
        listQuery: {
          limit: 6,
          page: 1
        }
      }
    },
    methods: {
      getNetList() {
        const url = this.quertList.image
        const page = this.listQuery.page
        console.log(url)
        // const baseUrl = `http://gushenxing.f3322.net:17006/pages/${url}?page=${this.listQuery.page}`
        fetchCVE(url, page)
          .then(res => {
            this.netList = res.data.data
            this.total = res.data.count * 6
          })
      },
      // handleSizeChange(val) {
      //   console.log(`每页 ${val} 条`)
      // },
      handleCurrentChange(val) {
        this.listQuery.page = val
        this.getNetList()
      }
    },
    created() {
      this.getNetList()
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .security-table
    margin: 30px 0 200px 0
    .ibox-content
      text-align: center
      table
        width: 100%
        border-collapse: collapse
        margin-bottom: 40px
        thead
          tr
            background: #F7F7F7
            height: 50px
            box-shadow: 4px 4px 20px #FAFAFA
            th
              font-size: 1.5em
              font-weight: bold
              color: #525252

            th:last-child
              display inline-block
              margin: 17px -50px 0 0
        tbody
          tr
            font-size: 1.4em
            color: #525252
            height: 60px
            padding: 0 20px
            i
            td:last-child
              a
                margin: 0 -100px 0 0
            td:last-child :hover
              color: #3E7AF6
              text-decoration: underline

  .divblock
    float right

  tr:hover
    background #FAFAFA

  #circle
    display inline-block
    width 8px
    height 7px
    background #F68C1F
    border-radius 4px
    margin -3px 6px 0 0
    i[id^="icon"]
      background url("../../main/img/icon.png")
      display: inline-block

    #icon-editor
      width: 18px
      height: 20px
      background-position: -0px -70px
    i[id^="icon-tools"]
      width: 18px
      height: 20px

    #icon-tools-copy
      background-position: -0px -70px
</style>
<style lang="stylus">
  .el-tooltip__popper
    max-width 400px
    line-height 180%

  .item-description
    width 100px
    height 20px
    overflow hidden
    white-space nowrap
    text-overflow ellipsis
</style>



